import React, { useState } from 'react';

const InterviewTest = () => {
  const [answers, setAnswers] = useState({});
  const [submitted, setSubmitted] = useState(false);

  const questions = [
    {
      id: 1,
      question: 'What is your experience with React?',
      type: 'text',
    },
    {
      id: 2,
      question: 'Rate your JavaScript skills from 1 to 10.',
      type: 'number',
    },
    // Add more questions as needed
  ];

  const handleChange = (id, value) => {
    setAnswers((prev) => ({ ...prev, [id]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Submit answers to AI evaluation service or backend
    setSubmitted(true);
  };

  if (submitted) {
    return (
      <div className="max-w-3xl mx-auto p-6 bg-white rounded shadow">
        <h2 className="text-2xl font-bold mb-4">Thank you for completing the interview test!</h2>
        <p>Your answers have been submitted for evaluation.</p>
      </div>
    );
  }

  return (
    <div className="max-w-3xl mx-auto p-6 bg-white rounded shadow">
      <h2 className="text-2xl font-bold mb-6">AI-Based Interview Test</h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        {questions.map(({ id, question, type }) => (
          <div key={id}>
            <label className="block font-semibold mb-1">{question}</label>
            {type === 'text' ? (
              <textarea
                className="w-full border rounded px-3 py-2"
                rows={4}
                onChange={(e) => handleChange(id, e.target.value)}
                required
              />
            ) : (
              <input
                type={type}
                className="w-full border rounded px-3 py-2"
                onChange={(e) => handleChange(id, e.target.value)}
                required
              />
            )}
          </div>
        ))}
        <button
          type="submit"
          className="px-4 py-2 bg-black text-white rounded hover:bg-gray-800 transition-colors duration-200"
        >
          Submit
        </button>
      </form>
    </div>
  );
};

export default InterviewTest;
