import React, { createContext, useState, useEffect, useContext } from 'react';
import { firebaseService } from '../services/firebaseService';

// Create the AuthContext with default empty object
const AuthContext = createContext({});

/**
 * AuthProvider component to wrap the app and provide authentication state and methods.
 */
export const AuthProvider = ({ children }) => {
  // State to hold the current authenticated user
  const [user, setUser] = useState(null);
  // State to indicate if auth state is loading
  const [loading, setLoading] = useState(true);
  // State to hold any auth error messages
  const [error, setError] = useState(null);

  // Effect to subscribe to Firebase auth state changes
  useEffect(() => {
    const unsubscribe = firebaseService.onAuthStateChanged((currentUser) => {
      setUser(currentUser);
      setLoading(false);
    });

    // Cleanup subscription on unmount
    return () => unsubscribe();
  }, []);

  /**
   * Login user with email and password
   * @param {string} email
   * @param {string} password
   */
  const login = async (email, password) => {
    setError(null);
    try {
      await firebaseService.login(email, password);
    } catch (err) {
      setError(err.message || 'Login failed');
      throw err;
    }
  };

  /**
   * Logout the current user
   */
  const logout = async () => {
    setError(null);
    try {
      await firebaseService.logout();
    } catch (err) {
      setError(err.message || 'Logout failed');
      throw err;
    }
  };

  /**
   * Register a new user with email and password
   * @param {string} email
   * @param {string} password
   */
  const register = async (email, password) => {
    setError(null);
    try {
      await firebaseService.register(email, password);
    } catch (err) {
      setError(err.message || 'Registration failed');
      throw err;
    }
  };

  // Provide user, loading, error, and auth methods to context consumers
  return (
    <AuthContext.Provider value={{ user, loading, error, login, logout, register }}>
      {children}
    </AuthContext.Provider>
  );
};

/**
 * Custom hook to use the AuthContext
 */
export const useAuth = () => useContext(AuthContext);
