import React, { createContext, useState, useContext, useEffect } from 'react';
import { subscribeToRoom, sendMessage } from '../services/chatService';

// Create ChatContext with default empty object
const ChatContext = createContext({});

/**
 * ChatProvider component to provide chat state and actions
 */
export const ChatProvider = ({ children }) => {
  // State to hold chat messages for the active room
  const [messages, setMessages] = useState([]);
  // State to hold the currently active chat room ID
  const [activeRoom, setActiveRoom] = useState(null);
  // State to hold any error messages
  const [error, setError] = useState(null);
  // State to indicate loading status for messages
  const [loading, setLoading] = useState(false);

  // Effect to subscribe to messages of the active room
  useEffect(() => {
    if (!activeRoom) {
      setMessages([]);
      return;
    }

    setLoading(true);
    setError(null);

    const unsubscribe = subscribeToRoom(
      activeRoom,
      (newMessages) => {
        setMessages(newMessages);
        setLoading(false);
      },
      (err) => {
        setError(err.message || 'Failed to load messages');
        setLoading(false);
      }
    );

    // Cleanup subscription on unmount or activeRoom change
    return () => unsubscribe();
  }, [activeRoom]);

  /**
   * Send a message to the specified chat room
   * @param {string} roomId
   * @param {string} message
   */
  const sendMessageToRoom = async (roomId, message) => {
    setError(null);
    try {
      await sendMessage(roomId, message);
    } catch (err) {
      setError(err.message || 'Failed to send message');
      throw err;
    }
  };

  // Provide chat state and actions to context consumers
  return (
    <ChatContext.Provider
      value={{ messages, activeRoom, setActiveRoom, sendMessage: sendMessageToRoom, error, loading }}
    >
      {children}
    </ChatContext.Provider>
  );
};

/**
 * Custom hook to use the ChatContext
 */
export const useChat = () => useContext(ChatContext);
