import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { getProjectById } from '../services/projectService';

const ProjectDetails = () => {
  const { id } = useParams();
  const [project, setProject] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchProject = async () => {
      try {
        const data = await getProjectById(id);
        setProject(data);
      } catch (error) {
        console.error('Failed to fetch project:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchProject();
  }, [id]);

  if (loading) {
    return <div>Loading project details...</div>;
  }

  if (!project) {
    return <div>Project not found.</div>;
  }

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded shadow">
      <h1 className="text-3xl font-bold mb-4">{project.title}</h1>
      <p className="mb-2"><strong>Client:</strong> {project.clientName}</p>
      <p className="mb-2"><strong>Budget:</strong> ₹{project.budget}</p>
      <p className="mb-2"><strong>Status:</strong> {project.status}</p>
      <p className="mb-4">{project.description}</p>
      {/* Add more detailed project info and actions here */}
    </div>
  );
};

export default ProjectDetails;
