import { initializeApp } from 'firebase/app';
import { getAuth, onAuthStateChanged, signInWithEmailAndPassword, createUserWithEmailAndPassword, signOut } from 'firebase/auth';
import { getFirestore } from 'firebase/firestore';

// Firebase configuration object from environment variables
const firebaseConfig = {
  apiKey: process.env.NEXT_PUBLIC_FIREBASE_API_KEY,
  authDomain: process.env.NEXT_PUBLIC_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.NEXT_PUBLIC_FIREBASE_PROJECT_ID,
  storageBucket: process.env.NEXT_PUBLIC_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.NEXT_PUBLIC_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.NEXT_PUBLIC_FIREBASE_APP_ID,
};

// Initialize Firebase app
const firebaseApp = initializeApp(firebaseConfig);

// Initialize Firebase Authentication and Firestore services
const auth = getAuth(firebaseApp);
const db = getFirestore(firebaseApp);

/**
 * Login user with email and password
 * @param {string} email
 * @param {string} password
 * @returns {Promise}
 */
const login = (email, password) => signInWithEmailAndPassword(auth, email, password);

/**
 * Register a new user with email and password
 * @param {string} email
 * @param {string} password
 * @returns {Promise}
 */
const register = (email, password) => createUserWithEmailAndPassword(auth, email, password);

/**
 * Logout the current user
 * @returns {Promise}
 */
const logout = () => signOut(auth);

/**
 * Subscribe to auth state changes
 * @param {function} callback
 * @returns {function} unsubscribe function
 */
const onAuthStateChangedListener = (callback) => onAuthStateChanged(auth, callback);

// Export firebaseService object with initialized services and auth methods
export const firebaseService = {
  firebaseApp,
  auth,
  db,
  login,
  register,
  logout,
  onAuthStateChanged: onAuthStateChangedListener,
};
