import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import MainLayout from '../layout/MainLayout';
import AuthLayout from '../layout/AuthLayout';

import Home from '../pages/Home';
import Login from '../pages/Login';
import RegisterDev from '../pages/RegisterDev';
import RegisterClient from '../pages/RegisterClient';
import DashboardDev from '../pages/DashboardDev';
import DashboardClient from '../pages/DashboardClient';
import ProjectList from '../pages/ProjectList';
import ProjectDetails from '../pages/ProjectDetails';
import InterviewTest from '../pages/InterviewTest';
import ChatRoom from '../pages/ChatRoom';
import LegalAgreement from '../pages/LegalAgreement';
import Referral from '../pages/Referral';
import Wallet from '../pages/Wallet';
import ProfileVerification from '../pages/ProfileVerification';
import Estimator from '../pages/Estimator';
import AdminPanel from '../pages/AdminPanel';
import NotFound from '../pages/NotFound';

const AppRoutes = () => {
  return (
    <Routes>
      {/* Public routes */}
      <Route element={<AuthLayout />}>
        <Route path="/login" element={<Login />} />
        <Route path="/register-dev" element={<RegisterDev />} />
        <Route path="/register-client" element={<RegisterClient />} />
      </Route>

      {/* Protected routes */}
      <Route element={<MainLayout />}>
        <Route path="/" element={<Home />} />
        <Route path="/dashboard-dev" element={<DashboardDev />} />
        <Route path="/dashboard-client" element={<DashboardClient />} />
        <Route path="/projects" element={<ProjectList />} />
        <Route path="/projects/:id" element={<ProjectDetails />} />
        <Route path="/interview-test" element={<InterviewTest />} />
        <Route path="/chat" element={<ChatRoom />} />
        <Route path="/legal-agreement" element={<LegalAgreement />} />
        <Route path="/referral" element={<Referral />} />
        <Route path="/wallet" element={<Wallet />} />
        <Route path="/profile-verification" element={<ProfileVerification />} />
        <Route path="/estimator" element={<Estimator />} />
        <Route path="/admin" element={<AdminPanel />} />
        <Route path="*" element={<NotFound />} />
      </Route>

      {/* Redirect unknown routes */}
      <Route path="*" element={<Navigate to="/" replace />} />
    </Routes>
  );
};

export default AppRoutes;
